import {AfterViewInit, Component, DoCheck, EventEmitter, Input, OnDestroy, OnInit, Output} from '@angular/core';
import {DayPilot} from "./core/daypilot-core";
import {optHash, rand} from "./util";

@Component({
  selector: 'daypilot-calendar',
  template: `
    <div id='{{id}}'></div>`,
  styles: [``]
})
export class DayPilotCalendarComponent implements OnInit, OnDestroy, AfterViewInit, DoCheck {

  public control!: DayPilot.Calendar;
  @Output() viewChange: EventEmitter<DayPilot.Date> = new EventEmitter<DayPilot.Date>();
  @Input() events: DayPilot.EventData[] = [];
  @Input() config: any;
  private _requestUpdateFull: boolean = false;
  private _requestUpdateEvents: boolean = false;
  private _requestViewChange: boolean = false;
  private _hashOptions: string = "";
  private _hashEvents: string = "";

  private _id: string = "dp_" + new Date().getTime() + rand();

  get id() {
    return this._id;
  }

  ngOnInit(): void {
  }

  ngOnDestroy(): void {
    this.dispose();
  }

  ngAfterViewInit(): void {
    this.dispose();
    let dp = new DayPilot.Calendar(this.id);
    this.control = dp;
    this.updateOptions();
    this.updateEvents();
    this._requestUpdateFull = false; // config just loaded and calling init(), no need to call update again
    this._requestUpdateEvents = false; // config just loaded and calling init(), no need to call update again
    dp.init();
  }

  ngDoCheck(): void {
    if (!this.control) {
      return;
    }
    this.updateOptions();
    this.updateEvents();
    if (this._requestUpdateFull) {
      this.control.update();
      this._requestUpdateFull = false;
      this._requestUpdateEvents = false;
    }
    else if (this._requestUpdateEvents) {
      this.control.update({ "events": this.events });
      this._requestUpdateEvents = false;
    }
    if (this._requestViewChange) {
      this._requestViewChange = false;
      let args: any = {};
      this.viewChange.emit(args);
    }
  }

  private dispose() {
    if (this.control) {
      this.control.dispose();
      //@ts-ignore
      this.control = null;
    }
  }

  private updateOptions(): void {
    let hash = optHash(this.config);
    if (hash !== this._hashOptions) {
      let dp = this.control as any;
      dp.internal.loadOptions(this.config);
      this._requestUpdateFull = true;
      this._requestViewChange = true;
    }
    this._hashOptions = hash;

  }

  private updateEvents(): void {
    let hash = optHash(this.events);
    if (hash !== this._hashEvents) {
      let dp = this.control;
      if (dp) {
        dp.events.list = this.events;
      }
      this._requestUpdateEvents = true;
    }
    this._hashEvents = hash;
  }
}
