import {AfterViewInit, Component, DoCheck, EventEmitter, Input, OnDestroy, OnInit, Output} from "@angular/core";
import {DayPilot} from "./core/daypilot-core";
import {optHash, rand} from "./util";

@Component({
  selector: 'daypilot-navigator',
  template: `
    <div id='{{id}}'></div>`,
  styles: [``]
})
export class DayPilotNavigatorComponent implements OnInit, OnDestroy, AfterViewInit, DoCheck {

  @Input() events: DayPilot.EventDataShort[] = [];
  @Input() config: any;
  @Output() dateChange: EventEmitter<DayPilot.Date> = new EventEmitter<DayPilot.Date>();
  public control!: DayPilot.Navigator;
  private _requestUpdate: boolean = false;
  private _hashOptions: string = "";
  private _hashEvents: string = "";
  private _onTrs: any = null;
  private _dateSet: boolean = false;
  private _currentDate: DayPilot.Date | null = null;

  private _date: DayPilot.Date = DayPilot.Date.today();

  public get date(): DayPilot.Date {
    return this._date;
  }

  // @Input() date: DayPilot.Date = DayPilot.Date.today();
  @Input()
  public set date(value: DayPilot.Date) {
    this._date = value;
    this._dateSet = true;
  }

  private _id: string = "dp_" + new Date().getTime() + rand();

  get id() {
    return this._id;
  }

  ngOnInit(): void {
  }

  ngOnDestroy(): void {
    this.dispose();
  }

  ngAfterViewInit(): void {
    this.dispose();
    this.control = new DayPilot.Navigator(this.id);
    let component = this;

    this.updateOptions();
    this.updateEvents();
    let dp = this.control;
    this.control.onTimeRangeSelected = function (args) {
      // emit event
      component.dateChange.emit(args.day);
      // call the original
      if (component._onTrs) {
        component._onTrs.call(dp, args);
      }
    };
    this._requestUpdate = false; // config just loaded and calling init(), no need to call update again
    this.control.init();

    if (this.control.selectionDay !== DayPilot.Date.today()) {
      component.dateChange.emit(this.control.selectionDay);
    }

  }

  ngDoCheck(): void {
    if (!this.control) {
      return;
    }
    this.updateOptions();
    this.updateEvents();
    if (this._requestUpdate) {
      this.control.update();
      this._requestUpdate = false;
    }

  }

  private dispose() {
    if (this.control) {
      this.control.dispose();
      //@ts-ignore
      this.control = null;
    }
  }

  private updateOptions(): void {
    let dp = this.control as any;
    let hash = optHash(this.config);

    if (hash !== this._hashOptions) {
      for (let name in this.config) {
        if (name === "selectionDay") {
          continue; // ignore
        }
        if (name === "onTimeRangeSelected") {
          this._onTrs = this.config.onTimeRangeSelected;
        } else {
          dp[name] = this.config[name];
        }
        this._requestUpdate = true;
      }
    }

    if (this._dateSet && this.date) {
      this._dateSet = false;
      this._currentDate = dp.selectionDay;
      dp.select(this.date);
    }

    this._hashOptions = hash;
  }

  private updateEvents(): void {
    let hash = optHash(this.events);
    if (hash !== this._hashEvents) {
      let dp = this.control;
      dp.events.list = this.events;
      this._requestUpdate = true;
    }
    this._hashEvents = hash;
  }
}
