import {AfterViewInit, Component, DoCheck, Input, OnDestroy, OnInit} from "@angular/core";
import {DayPilot} from "./core/daypilot-core";
import {optHash, rand} from "./util";

@Component({
  selector: 'daypilot-month',
  template: `
    <div id='{{id}}'></div>`,
  styles: [``]
})
export class DayPilotMonthComponent implements OnInit, OnDestroy, AfterViewInit, DoCheck {

  public control!: DayPilot.Month;
  @Input() events: DayPilot.EventData[] = [];
  @Input() config: any;
  private _requestUpdate: boolean = false;
  private _hashOptions: string = "";
  private _hashEvents: string = "";

  private _id: string = "dp_" + new Date().getTime() + rand();

  get id() {
    return this._id;
  }

  ngOnInit(): void {
  }

  ngOnDestroy(): void {
    this.dispose();
  }

  ngAfterViewInit(): void {
    this.dispose();
    this.control = new DayPilot.Month(this.id);
    this.updateOptions();
    this.updateEvents();
    this._requestUpdate = false; // config just loaded and calling init(), no need to call update again
    this.control.init();
  }

  ngDoCheck(): void {
    if (!this.control) {
      return;
    }
    this.updateOptions();
    this.updateEvents();
    if (this._requestUpdate) {
      this.control.update();
      this._requestUpdate = false;
    }
  }

  private dispose() {
    if (this.control) {
      this.control.dispose();
      //@ts-ignore
      this.control = null;
    }
  }

  private updateOptions(): void {
    let hash = optHash(this.config);
    if (hash !== this._hashOptions) {
      let dp = this.control as any;
      dp.internal.loadOptions(this.config);
      this._requestUpdate = true;
    }
    this._hashOptions = hash;
  }

  private updateEvents(): void {
    let hash = optHash(this.events);
    if (hash !== this._hashEvents) {
      let dp = this.control;
      dp.events.list = this.events;
      this._requestUpdate = true;
    }
    this._hashEvents = hash;

  }
}
